import React, {useState, useEffect} from "react";
import Breadcrumb from "../Breadcrumb";
import { Container, Row, Col, Table } from "react-bootstrap";
import placeholderImg from "../../../assets/images/placeholderImg.svg";
import prevArrow from "../../../assets/images/prev.svg";
import nextArrow from "../../../assets/images/next.svg";

import { Link } from "react-router-dom";
import { apiGet, apiPost, axiosWithAuth } from "../../../utilities/axiosInstance";
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { useAuth } from "../../../context/AuthContext";
import Loader from "../../../Components/Loader";
import { useNavigate } from 'react-router-dom';

export default function BookingHisotry() {
       const {  userData, token } = useAuth();
       const [historyBookings, setHistoryBookings]= useState([])
      const [isLoading, setIsLoading]= useState(false);
      const [tablePage, setTablePage] = useState(1)
      const clickTablepage = (nowpage) => { setTablePage(nowpage) };
      const navigate = useNavigate();
      useEffect(() => {
    
        const fetchTrainerDashData = async () => {
          setIsLoading(true)
          try {
             const authInstance = axiosWithAuth(token); // Pass the token to create the instance 
          const responseNew = await authInstance.get(`${API_ENDPOINTS.BOOKING_LIST}?status=history`);
             const response = await responseNew.data
        // console.log(response)
            if (response.success && response.data) {
              setIsLoading(false)
             // console.log(response)   
              setHistoryBookings(response.data)  
            } else {
              console.error("Unexpected response format:", response);
            }
          } catch (error) {
            console.error("Error fetching pricing data:", error);
          }
          finally{
            setIsLoading(false)
          }
        };
    
        fetchTrainerDashData();
      },[])
      
  // const bookings = [
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "CANCELLED (REFUNDED)",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "CANCELLED (REFUNDED)",
  //   },
  //   {
  //     id: "#20465830392",
  //     learner: "Anthony S.",
  //     date: "Fri, 08 Dec 2024",
  //     time: "9.30am-10.30am",
  //     location: "123 Main Street, Perth 6000 WA",
  //     status: "COMPLETED PAYMENT",
  //   },
  // ];

  const getStatusClass = (status) => {
    if (status.includes("canceled")) return "text-red";
    if (status.includes("confirmed")) return "text-green";
    return "text-secondary";
  };
  return (
    <div>
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
        {isLoading && <Loader />}
          <Container>
            <Row>
              <Col lg={12}>
                {/* Upcoming Booking */}
                <div className="upcomingBooking">
                  <Link onClick={() => navigate(-1)} className="go_back">&lt; Go back</Link>
                  <div className="secIntro ">
                    <div className="secIntro__left">
                      <h3>Bookings History </h3>
                    </div>
                  </div>
                  <div className="accordians">
                    <div className="accordians__card">
                      <div className="accordians__description">
                      <Table>
                        <thead>
                          <tr>
                            <th>Booking</th>
                            <th>Learner</th>
                            <th>Date</th>
                            <th>Time</th>
                            <th>Location</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                        {isLoading ? <tr><td>Loading...</td></tr> :
                          // pendingBookings.map((booking, index) => (
                            historyBookings?.slice(tablePage * 5 - 5, tablePage * 5).map((booking, index) =>(
                         // {historyBookings.map((booking, index) => (
                            <tr key={index}>
                              <td>{booking.bookingNumber}</td>
                              <td>
                                <img
                                  src={booking?.learners?.profile_picture || placeholderImg}
                                  alt="profile"
                                  className="rounded-circle mr-2"
                                />{" "}
                                {/* {booking?.instructor?.first_name} */}
                                <span className="str-reduce">{booking?.learners?.myself === 1 ? `${booking?.learners?.first_name} ${booking?.learners?.last_name}` : `${booking?.learners?.learner_first_name} ${booking?.learners?.learner_last_name}`}
                                </span>
                              </td>
                              <td>{booking?.lesson_details?.lesson_date}</td>
                              <td>{booking?.lesson_details?.lesson_time}</td>
                              <td>{booking?.learners?.address}</td>
                              <td
                                className={`font-weight-bold ${getStatusClass(
                                  booking?.booking_status
                                )}`}
                              >
                                <div className="d-flex align-items-center">
                                  <div className="statusIco"></div>
                                  {booking.booking_status}
                                </div>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                        {/* Pagination */}
                        <div className='pagination-container'>
                            <button className="btn " onClick={() => { clickTablepage(tablePage - 1) }} 
                              style={{'pointer-events':tablePage === 1 ? 'none' : ''}}  >
                              <img src={prevArrow} alt="" />
                            </button>
                            {historyBookings.length > 5 ? <span className={tablePage === 1 ? "active_tab" : "pagetab"} onClick={() => clickTablepage(1)}> 1 </span> :'' }
                     
                          {/* Left Ellipsis */}
                            {tablePage > 3 && <span className="pagetab"> ... </span>}
                         
                          {/* Visible Pages */}
                            {[...Array(3)].map((_, i) => {
                                const page = tablePage - 1 + i; // Adjust the visible pages around the current page
                                return ( page > 1 &&
                                    page < Math.ceil(historyBookings.length / 5) && (
                                        <span key={page} className={tablePage === page ? "active_tab" : ""} onClick={() => clickTablepage(page)}>
                                            {page}
                                        </span>
                                    )
                                );
                            })}

                            {/* Right Ellipsis */}
                            {tablePage < Math.ceil(historyBookings.length / 5) - 2 && ( <span className="pagetab"> ... </span> )}

                            {/* Last Page */}
                            <span className={ tablePage === Math.ceil(historyBookings.length / 5) ? "active_tab" : "pagetab" }
                                onClick={() => clickTablepage(Math.ceil(historyBookings.length / 5))}
                            >
                                {Math.ceil(historyBookings.length / 5)}
                            </span>

                          <button className="btn " 
                             style={{'pointer-events': tablePage === Math.ceil(historyBookings.length / 5) ? 'none' : ''}} 
                             onClick={() => { clickTablepage(tablePage + 1); }}>
                             <img src={nextArrow} alt="" />
                          </button>
                          </div>

                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
}
